import React, { FC, useEffect, useRef, useState } from "react";

import { ProgressBar } from "../ProgressBar";
import { colors } from "../styleguide";
import FileIcon from "../styleguide/images/fileIcons/FileIcon";
import { pxToRem } from "../utils";

import {
	FullWidthRow,
	StyledCard,
	StyledIconButton,
	StyledProgressWrapper,
	StyledTitle,
	StyledTitleWrapper,
} from "./UploadProgressBar.styles";
import {
	UploadProgressBarProps,
} from "./UploadProgressBar.types";

const UploadProgressBar: FC<UploadProgressBarProps> = ({
	dataQA,
	ariaLabel,
	className,
	title,
	subtitle,
	fileIcon,
	mt,
	mb,
	ml,
	mr,
	m,
	onCancel,
	progressBar,
}) => {
	const titleRef = useRef<HTMLDivElement>(null);
	const subTitleRef = useRef<HTMLDivElement>(null);
	const [hasTooltip, setHasTooltip] = useState({ subtitle: false, title: false });

	useEffect(() => {
		if (subTitleRef || titleRef) {
			if (titleRef?.current && subTitleRef?.current) {
				const shouldHaveTitleTooltip = titleRef?.current.offsetWidth < titleRef?.current.scrollWidth;
				setHasTooltip({
					title: shouldHaveTitleTooltip,
					subtitle: shouldHaveTitleTooltip || subTitleRef?.current.offsetWidth < subTitleRef?.current.scrollWidth,
				});
			}
		}
	}, [subtitle, title]);

	return (
		<StyledCard
			dataQA={dataQA}
			ariaLabel={ariaLabel}
			className={className}
			mt={mt}
			mb={mb}
			ml={ml}
			mr={mr}
			m={m}
		>
			<FileIcon fileName={fileIcon} />
			<StyledProgressWrapper>
				<FullWidthRow>
					<StyledTitleWrapper>
						<StyledTitle
							dataQA="upload-progress-bar-title"
							variant="bodyCopyBold"
							title={hasTooltip.title ? title : undefined}
							ref={titleRef}
						>
							{title}
						</StyledTitle>
						{subtitle && <StyledTitle
							dataQA="upload-progress-bar-subtitle"
							variant="bodyCopySmall"
							ml={pxToRem(10)}
							title={hasTooltip.subtitle ? subtitle : undefined}
							ref={subTitleRef}
							color={`${colors.black}A8`}
						>
							{subtitle}
						</StyledTitle>}
					</StyledTitleWrapper>
					<StyledIconButton
						size="Small"
						icon="CircleX"
						onClick={onCancel}
						variant="tertiary"
						dataQA="cancel-button" />
				</FullWidthRow>
				<FullWidthRow mr={pxToRem(6)}>
					<ProgressBar {...progressBar} />
				</FullWidthRow>
			</StyledProgressWrapper>
		</StyledCard >
	);
};

UploadProgressBar.displayName = "UploadProgressBar";
export default UploadProgressBar;
